<template>
  <div class="bg-zinc-950 min-h-screen text-gray-300 w-full flex flex-col items-center">
    <div class="w-11/12 lg:w-4/5 bg-zinc-900 my-6 rounded-xl text-2xl px-5 py-2 lg:py-1">
      <div class="font-bold mb-2">Instructions:</div>
      <div class="text-lg text-gray-400 lg:text-sm">
        <p>1. Write the Name of the Student as it should be in the Marksheet.</p>
        <p>2. In case of NG, just put the marks, system will detect NG.</p>
        <p>3. Enter the number of marks scored.</p>
        <p>4. Use "-" to separate the dates. Don't use "/".</p>
      </div>
    </div>

    <div class="w-11/12 lg:w-4/5 h-auto bg-zinc-900 rounded-xl px-4 py-3 lg:py-2">
      <div class="text-2xl text-gray-300 font-bold mt-5 lg:text-xl lg:mt-2">
        Enter the details of the software:
      </div>
      <div class="mt-5 bg-zinc-900 px-5 py-5 rounded-2xl">
        <form @submit.prevent="" @keydown.prevent.enter="">
          <div class="flex flex-col lg:flex-row lg:space-x-5">
            <div
              class="w-full lg:w-2/5 bg-zinc-900 px-3 py-3 rounded-2xl lg:rounded-r-none flex flex-col my-3 space-y-5 lg:px-1 ">
              <input type="number" placeholder="Registration Number" v-model="student.reg_no" @blur="queryreq()"
                class="w-full bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl lg:w-48">
              <!-- <button class="bg-green-700 px-2 py-1  rounded-2xl text-white w-1/2">Query Registration Number</button> -->
              <input type="text" placeholder="Name of the Student" v-model="student.name"
                class="w-full bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl lg:w-48">

              <input type="number" placeholder="Symbol Number" v-model="student.symb_no"
                class="w-full bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl lg:w-48">
              <input type="text" placeholder="DOB in BS(YYYY-MM-DD)" v-model="student.dob_bs" @blur="dateconv()"
                class="w-full bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl date lg:w-52">
              <input type="text" placeholder="DOB in AD(YYYY-MM-DD)" v-model="student.dob_ad"
                class="w-full bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl date lg:w-52">
            </div>
            <div class="w-full bg-zinc-900 px-5 py-3 rounded-2xl lg:rounded-l-none flex flex-col my-3 space-y-5 lg:px-0"
              v-if="subjects_to_send.length != 0">
              <div class="flex flex-col lg:flex-row lg:space-x-2" v-for="(i, index) in subjects" :key="index">
                <div
                  class="w-full lg:w-60 mx-2 bg-transparent border-gray-500 px-3 py-1 rounded-xl font-bold lg:text-sm ">
                  {{ subjects_to_send[index].subject }}</div>
                <input type="number" step="any" placeholder="Marks in TH"
                  v-model="subjects_to_send[index].theory_marks_obtained"
                  class="w-full lg:w-32 mx-2 bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl">
                <input type="number" step="any" placeholder="Marks in IN"
                  v-model="subjects_to_send[index].practical_marks_obtained"
                  class="w-full lg:w-32 mx-2 bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl">
              </div>
            </div>
          </div>

          <button v-if="response_code_1 === 200"
            class="rounded-xl px-5 py-2 bg-green-600 hover:bg-green-700 transition-colors ease-in-out mt-5 lg:mt-2"
            @click="bool_dialog = true">
            Update
          </button>
          <button v-if="response_code_1 !== 200"
            class="rounded-xl px-5 py-2 bg-green-600 hover:bg-green-700 transition-colors ease-in-out mt-5 lg:mt-2"
            @click="bool_dialog = true">
            Submit
          </button>
        </form>
      </div>
    </div>

    <div v-if="bool_dialog"
      class="fixed bg-black w-80 md:w-[33vw] h-48 top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-neutral-200 rounded-2xl py-5 px-3 flex flex-col justify-between">
      <div class="text-2xl px-2 py-4 text-center" v-if="response_code_1 ===200">Do you want to update the data?</div>
    <div class="text-2xl px-2 py-4 text-center" v-if="response_code_1 !=200">Do you want to add the data?</div>
      <div class="flex justify-end">
        <button class="px-10 py-2 bg-green-700 rounded-2xl mx-2"
          @click="handle_submit(); bool_dialog = false">Yes</button>
        <button class="px-10 py-2 bg-red-700 rounded-2xl" @click="bool_dialog = false">No</button>
      </div>
    </div>

    <div v-if="(response_code === 201 || response_code ===200) && display_msg"
      class="fixed bg-green-800 right-10 bottom-10 px-4 rounded-2xl text-2xl text-center py-3">
      Subject Added successfully
    </div>
    <div v-if="(response_code !== 201 || response_code !== 200) && display_msg"
      class="fixed bg-red-800 right-10 bottom-10 w-60 rounded-2xl text-2xl text-center py-3">
      {{error_message}}
    </div>
    <div v-if="invalid_marks"
      class="fixed bg-red-800 right-10 bottom-10 w-60 rounded-2xl text-2xl text-center py-3">
      Marks is out of range. Check marks
    </div>
  </div>
</template>
<script>
import axios from 'axios'
// import NepaliDate from 'nepali-date-converter'

import NepaliDate from 'nepali-datetime'
import dateConverter from 'nepali-datetime/dateConverter'
export default {
  name: "StudentView",
  watch: {
    subjects: {
      deep: true,
      handler(newVal) {
        // Update subjects_to_send[index].subject when subjects[i].name changes
        newVal.forEach((subject, index) => {
          this.subjects_to_send[index].subject = subject.name;

        });
      }
    },

  },
  data() {
    return {
      subjects: [],
      student: {
        name: '',
        reg_no: null,
        symb_no: null,
        dob_bs: '',
        dob_ad: ''
      },
      subjects_to_send: [],

      dataArray: [],
      bool_dialog: false,
      display_msg: false,
      response_code: null,
      response_code_1: null,
      invalid_marks: false,
      error_message:""


    }
  },
  mounted() {
    this.get_subjects();
  },
  methods: {
    dateconv() {
      // console.log(dateConverter.nepaliToEnglish(this.student.dob_bs))
      const date1 = new NepaliDate(this.student.dob_bs)
      // console.log(this.student.dob_bs)
      // console.log(date1.getAD)
      console.log(`${date1.getYear()}-${date1.getMonth() + 1}-${date1.getDate()}`)

      // const [enYear, enMonth, enDay] = dateConverter.nepaliToEnglish(2063,6,15)
      const [enYear, enMonth, enDay] = dateConverter.nepaliToEnglish(date1.getYear(), date1.getMonth() + 1, date1.getDate())
      this.student.dob_ad = `${enYear}-${enMonth}-${enDay}`
      // console.log(dateConverter.nepaliToEnglish(2063,6,15))
    },
    async queryreq() {
      try{
        const responsen = await axios.post("/get/student", {
        "reg_no": this.student.reg_no
      }).catch((error) => {console.log(error)})
      this.response_code_1 = responsen.status
      if (responsen.status === 200) {
        this.student = responsen.data.student
        // console.log(responsen.data.marks)
        for (var index = 0; index < this.subjects_to_send.length; index++) {
          console.log(index)
          this.subjects_to_send[index].theory_marks_obtained = responsen.data.marks[index].theory_marks_obtained
          this.subjects_to_send[index].practical_marks_obtained = responsen.data.marks[index].practical_marks_obtained
        }
      }
      else{
        console.log("No user found")
      }
      }
      catch(error){
        console.log("No user found")
      }
      
      // console.log(responsen)
    },
    dialogbox() {
      this.bool_dialog = !this.bool_dialog
    },
    add_arrays() {
      for (var a in this.subjects) {

        this.subjects_to_send.push({
          subject: "",
          theory_marks_obtained: null,
          practical_marks_obtained: null,
        });

      }
    },
    async handle_submit() {
      for (var i in this.subjects_to_send) {
        if (i.theory_marks_obtained > 75 || i.practical_marks_obtained > 25 || i.theory_marks_obtained <0 ||i.practical_marks_obtained <0) {
          console.log(i)
          this.invalid_marks = true
        }
      }
      if (this.invalid_marks === false) {
        const payload = {
          student: this.student,
          subjects: this.subjects_to_send
        }
        // console.log(payload)
        const response = await axios.post("/create/mark", payload).catch(error => {
          console.error(error)
          this.display_msg = true
        })
        if (response || response.status === 201) {
          this.response_code = response.status
          this.student = {
            name: '',
            reg_no: null,
            symb_no: null,
            dob_bs: '',
            dob_ad: ''
          }
          for (var index = 0; index < this.subjects_to_send.length; index++) {
            // console.log(index)
            this.subjects_to_send[index].theory_marks_obtained = null
            this.subjects_to_send[index].practical_marks_obtained = null
          }


        }
        this.error_message = response.statusText

        setTimeout(() => {
          this.display_msg = false
        }, 3000);
      }

    },
    async get_subjects() {
      const response = await axios.get("/get/subjects",
        {
          Headers: {
            "Access-Control-Allow-Origin": "*"
          }
        }
      )
      if (response.status === 200) {
        this.subjects = response.data
        this.add_arrays()
      }
      // console.log(this.subjects)

    }
  },

}
</script>

<style>
input[type="date"]::-webkit-calendar-picker-indicator {
  color: rgba(0, 0, 0, 0);
  opacity: 1;
  display: block;
  background: url(https://mywildalberta.ca/images/GFX-MWA-Parks-Reservations.png) no-repeat;
  width: 20px;
  height: 20px;
  border-width: thin;
}
</style>