<template>
    <div class="bg-zinc-950 text-gray-300 w-screen min-h-screen flex flex-col items-center">
      <div class="w-11/12 lg:w-4/5 bg-zinc-900 my-6 rounded-xl text-2xl px-5 py-2">
        <div class="font-bold mb-2">Instructions:</div>
        <div class="text-lg text-gray-400">
          <p>1. Write the Name of the Student as it should be in the Marksheet.</p>
          <p>2. In case of NG, just put the marks, system will detect NG.</p>
          <p>3. Enter the number of marks scored.</p>
          <p>4. Use "-" to separate the dates. Don't use "/".</p>
          <p>5. Click on Submit to get the data. If you want the previous export, then directly click getPDF.</p>
        </div>
      </div>
  
      <div class="w-11/12 lg:w-4/5 h-auto bg-zinc-900 rounded-xl px-4 py-3">
        <div class="text-2xl text-gray-300 font-bold mt-5">Enter the details:</div>
        <div class="mt-5 bg-zinc-950 px-5 py-5 rounded-2xl">
          <form @submit.prevent="" @keydown.prevent.enter="">
            <div class="flex flex-col lg:flex-row lg:space-x-5">
              <div class="w-full lg:w-2/5 bg-zinc-900 px-5 py-3 rounded-2xl flex flex-col my-3 space-y-5">
                <input type="text" placeholder="Exam Year (BS)" v-model="exam_year_bs"
                       class="w-full bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl">
                <input type="text" placeholder="Exam Year (AD)" v-model="exam_year_ad"
                       class="w-full bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl">
                <input type="text" placeholder="Date of Issue" v-model="date_of_issue"
                       class="w-full bg-transparent border-2 border-gray-500 px-3 py-1 rounded-xl">
              </div>
            </div>
            <button class="rounded-xl px-5 py-2 bg-green-600 hover:bg-green-700 transition-colors ease-in-out mt-5"
                    @click="bool_dialog=true">
              Submit
            </button>
          </form>
        </div>
      </div>
  
      <div v-if="bool_dialog"
           class="fixed bg-black w-80 md:w-[33vw] h-48 top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-neutral-200 rounded-2xl py-5 px-3 flex flex-col justify-between">
        <div class="text-2xl px-2 py-4 text-center">Do you want to export the marksheet?</div>
        <div class="flex justify-end">
          <button class="px-10 py-2 bg-green-700 rounded-2xl mx-2" @click="handle_submit(); bool_dialog = false; showmsg()">Yes</button>
          <button class="px-10 py-2 bg-red-700 rounded-2xl" @click="bool_dialog = false">No</button>
        </div>
      </div>
  
      <div v-if="response_code === 200 && display_msg"
           class="fixed bg-green-800 right-10 bottom-10 px-4 rounded-2xl text-2xl text-center py-3">
        Wait for the download prompt
      </div>
      <div v-if="response_code !== 200 && display_msg"
           class="fixed bg-red-800 right-10 bottom-10 w-60 rounded-2xl text-2xl text-center py-3">
        Error in the data!
      </div>
    </div>
  </template>
<script>
import axios from 'axios'
export default {
    name: "ExportView",
    data() {
        return {
            exam_year_bs: null,
            exam_year_ad: null,
            date_of_issue: null,
            bool_dialog: false,
            response_code: null,
            display_msg: false
        }
    },
    methods: {
        showmsg(){
            setTimeout(() => {
        this.display_msg = false
      }, 3000)
        },
        async handle_submit() {
            const response = await axios.post("/test/data", {
                exam_year_ad: this.exam_year_ad,
                exam_year_bs: this.exam_year_bs,
                date_of_issue: this.date_of_issue
            }).catch(error => {
                console.error(error)
            })
            console.log(response)
            this.response_code = response.status
            this.display_msg = true
            this.getPDF()
            

        },
        async getPDF() {
            axios.get('/export/pdf', {
                responseType: 'blob' // Important to receive the PDF as a blob
            })
                .then(response => {
                    const url = window.URL.createObjectURL(new Blob([response.data]));
                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', 'document.pdf');
                    document.body.appendChild(link);
                    link.click();
                })
                .catch(error => {
                    console.error('Error downloading PDF:', error);
                });



        }
    }
}
</script>