<template>
  <div class="bg-zinc-950 text-gray-300 max-w-[90vw] md:max-w-[70vw] h-screen p-4">
    <div class="w-full md:w-4/5 bg-zinc-900 my-6 rounded-xl text-2xl px-5 py-2">
      <div class="font-bold mb-2">Instructions:</div>
      <div class="text-lg text-gray-400">
        <p>1. Write the Name of the subject as it should be in the Marksheet.</p>
        <p>2. You don't Need to specify Theory (TH) and Practical (PR).</p>
        <p>3. You only need to enter the subject once.</p>
      </div>
    </div>
    <div class="h-3/4 w-full bg-zinc-900 rounded-xl px-1 py-3 lg:px-4">
      <div class="text-2xl text-gray-300 font-bold mt-5 lg:text-xl">
        Enter the details of the Subjects:
      </div>
      <div class="mt-5 mx-auto bg-zinc-950 px-1 py-5 rounded-2xl lg:px-1">
        <form class="" @submit.prevent="" @keydown.prevent.enter="">
          <div class="w-full bg-zinc-900 px-2 md:px-10 py-3 rounded-2xl flex flex-wrap my-3 text-sm space-y-3 md:space-y-0" v-for="(subject, index) in subjects" :key="index">
            <div class="mr-2 text-xl text-gray-300">{{ index + 1 }}.</div>
            <input type="text" v-model="subjects[index].name" :placeholder="'Subject ' + (index + 1)" class="w-full md:w-64 bg-transparent border-2 border-gray-500 px-1 py-1 rounded-xl mb-2 md:mb-0">
            <input type="text" v-model="subjects[index].theory_subject_code" placeholder="Subject Code (TH)" class="w-full md:w-36 md:ml-2 bg-transparent border-2 border-gray-500 px-1 py-1 rounded-xl mb-2 md:mb-0">
            <input type="number" step="any" v-model="subjects[index].theory_credit_hours" placeholder="Credit Hour (TH)" class="w-full md:w-36 md:ml-2 bg-transparent border-2 border-gray-500 px-1 py-1 rounded-xl mb-2 md:mb-0">
            <input type="number" step="any" v-model="subjects[index].theory_marks" placeholder="Full Marks (TH)" class="w-full md:w-28 md:ml-2 bg-transparent border-2 border-gray-500 px-1 py-1 rounded-xl mb-2 md:mb-0">
            <input type="text" v-model="subjects[index].practical_subject_code" placeholder="Subject Code (IN)" class="w-full md:w-32 md:ml-2 bg-transparent border-2 border-gray-500 px-1 py-1 rounded-xl mb-2 md:mb-0">
            <input type="number" step="any" v-model="subjects[index].practical_credit_hours" placeholder="Credit Hour (IN)" class="w-full md:w-28 md:ml-2 bg-transparent border-2 border-gray-500 px-1 py-1 rounded-xl mb-2 md:mb-0">
            <input type="number" step="any" v-model="subjects[index].practical_marks" placeholder="Full Marks (IN)" class="w-full md:w-28 md:ml-2 bg-transparent border-2 border-gray-500 px-1 py-1 rounded-xl mb-2 md:mb-0">
          </div>
          <div class="flex flex-col md:flex-row justify-end mt-5">
            <button class="rounded-xl px-5 py-2 bg-green-600 hover:bg-green-700 transition-colors ease-in-out mb-3 md:mb-0 md:mr-3" @click="addSubject">Add Subject</button>
            <button class="rounded-xl px-5 py-2 bg-green-600 hover:bg-green-700 transition-colors ease-in-out" @click="dialogbox">Submit</button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div class="absolute bg-black w-[80vw] md:w-[33vw] h-48 top-0 bottom-0 left-0 right-0 m-auto text-neutral-200 rounded-2xl py-5 px-3 flex flex-col justify-between" v-if="bool_dialog">
    <div class="text-2xl px-2 py-4 text-center">Do you want to add the subjects?</div>
    <div class="flex justify-end">
      <button class="px-10 py-2 bg-green-700 rounded-2xl mx-2" @click="collectData(); bool_dialog=false">Yes</button>
      <button class="px-10 py-2 bg-red-700 rounded-2xl" @click="bool_dialog=false">No</button>
    </div>
  </div>
  <div class="absolute bg-green-800 right-5 md:right-10 bottom-5 md:bottom-10 px-4 rounded-2xl text-2xl text-center py-3" v-if="response_code === 201 && display_msg">Subject Added successfully</div>
  <div class="absolute right-5 md:right-10 bottom-5 md:bottom-10 w-60 rounded-2xl bg-red-800 text-2xl text-center py-3" v-if="response_code != 201 && display_msg">Error in the data!</div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

import axios from 'axios'
export default {
  name: 'HomeView',
  components: {

  },

  data() {
    return {
      subjects: [{
        name: "",
        theory_subject_code: "",
        theory_credit_hours: null,
        theory_marks: null,
        practical_subject_code: "",
        practical_credit_hours: null,
        practical_marks: null
      },],
      dataArray: [],
      bool_dialog : false,
      response_code : null,
      display_msg:false

    }
  },
  methods: {
    addSubject() {
      this.subjects.push({
        name: "",
        theory_subject_code: "",
        theory_credit_hours: null,
        theory_marks: null,
        practical_subject_code: "",
        practical_credit_hours: null,
        practical_marks: null
      });
    },
    dialogbox(){
      this.bool_dialog = !this.bool_dialog
    },

    async collectData() {
      // Clear the array before collecting data
      this.dataArray = [];
      // Filter out empty objects and push each subject into the dataArray
      this.subjects.forEach(subject => {
        if (subject.name !== '') { // Check if name property is not empty
          this.dataArray.push({
            name: subject.name,
            theory_credit_hours: subject.theory_credit_hours,
            theory_marks: subject.theory_marks,
            practical_credit_hours: subject.practical_credit_hours,
            practical_marks: subject.practical_marks,
            theory_subject_code: subject.theory_subject_code,
            practical_subject_code: subject.practical_subject_code
            // You can add more properties here if needed
          });
        }
      });
      // Log the collected data
      const response = await axios.post("/create/subject",this.dataArray).then(this.display_msg = true)
      this.response_code =response.status

      setTimeout(() => {
        this.display_msg = false
      }, 3000);
      // Now you have the data array ready to be used or sent to an API
    }
  }

}



</script>
